import React, { Component } from "react";
import _ from "lodash";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { Field, reduxForm, change } from "redux-form";
import { addUser, ADD_USER } from "../../actions";
import cx from "classnames";
import DotLoader from "../Utils/DotLoader";
import { RenderField, required, email, number } from "../Utils/FormField";

class AddUserModal extends Component {
  onSubmitForm(user) {
    this.props.addUser(user).then(resp => {
      if (resp.type === ADD_USER) this.props.history.goBack();
    });
  }

  renderError() {
    if (this.props.errorMassage) {
      return (
        <div className="alert alert-danger">
          <strong>Akey!!</strong>
          {this.props.errorMassage}
        </div>
      );
    }
  }

  render() {
    const { handleSubmit, pristine, reset, submitting } = this.props;

    return (
      <div className="modal-darbar">
        <div className="modal-back" onClick={this.props.history.goBack} />
        <div className="modal">
          <form onSubmit={handleSubmit(this.onSubmitForm.bind(this))}>
            <div className="form-item">
              <Field name="email" component={RenderField} label=" " validate={[required, email]} />
              <Field name="name" component={RenderField} label=" " validate={required} />
              <Field name="familyName" component={RenderField} label="  " validate={required} />
              <Field name="phone" component={RenderField} label=" " validate={[required, number]} />
              <Field name="password" component={RenderField} label="  " type="password" validate={required} />
              <Field name="doctor" component={RenderField} label="  " type="checkbox" />
              <Field name="phoneValidate" component={RenderField} label="   " type="checkbox" />

              <div className="form-tak taki">
                <label>  </label>
                <div className="form-field-div">
                  <Field name="level" component="select" className="form-field-field">
                    <option />
                    <option value="normal"></option>
                    <option value="expert"></option>
                    <option value="owner"></option>
                    <option value="editor"></option>
                    <option value="auther"></option>
                    <option value="admin"></option>
                  </Field>
                </div>
              </div>
            </div>

            {this.renderError()}
            <div className="chapchin width-same">
              <button type="submit" className="dogme i-round i-abi" disabled={submitting}>
                
              </button>

              <span onClick={this.props.history.goBack} className="dogme i-round i-tosi">
                {" "}
                {" "}
              </span>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

AddUserModal = reduxForm({ form: "AddUserModal" })(AddUserModal);

export default connect(
  null,
  { addUser }
)(AddUserModal);
